import React, { useState } from 'react';
import StatsOverview from './StatsOverview';
import RecentlyMigratedTokens from './RecentlyMigratedTokens';
import TrendingTokens from './TrendingTokens';
import NewTokenCardsUpdated from './NewTokenCardsUpdated';

const Dashboard = ({ isDarkMode, searchQuery = '' }) => {
  const [activeFilter, setActiveFilter] = useState('all');

  const filterOptions = [
    { id: 'all', label: 'All Tokens' },
    { id: 'new', label: 'New' },
    { id: 'upcoming', label: 'Upcoming' },
    { id: 'migrated', label: 'Migrated' }
  ];

  return (
    <div className="dashboard-container">
      {/* Stats Overview */}
      <StatsOverview isDarkMode={isDarkMode} />

      {/* Top Migration Threshold */}
      <RecentlyMigratedTokens isDarkMode={isDarkMode} />

      {/* Trending Tokens */}
      <TrendingTokens isDarkMode={isDarkMode} />

      {/* Token Cards Section */}
      <div className="token-section">
        {/* Filter Tabs */}
        <div className="filter-container">
          {filterOptions.map((option) => (
            <button
              key={option.id}
              onClick={() => setActiveFilter(option.id)}
              className={`filter-btn ${activeFilter === option.id ? 'active' : ''}`}
            >
              {option.label}
            </button>
          ))}
        </div>

        {/* Token Cards */}
        <NewTokenCardsUpdated 
          activeFilter={activeFilter}
          isDarkMode={isDarkMode}
          searchQuery={searchQuery}
        />
      </div>
    </div>
  );
};

export default Dashboard; 