import React from 'react';
import { useNavigate } from 'react-router-dom';
import { FiTrendingUp, FiChevronLeft, FiChevronRight, FiArrowRight } from 'react-icons/fi';

const TrendingTokens = ({ isDarkMode }) => {
  const navigate = useNavigate();
  
  const trendingTokens = [
    { name: "BaseToken", symbol: "BASE", price: "$2.45", change: "+12.5%", logo: "B", tokenId: "base" },
    { name: "DexFi", symbol: "DEXF", price: "$1.22", change: "+8.3%", logo: "D", tokenId: "dexf" },
    { name: "CurveCoin", symbol: "CRV", price: "$0.89", change: "+15.7%", logo: "C", tokenId: "crv" },
    { name: "LiquidFi", symbol: "LIQF", price: "$3.12", change: "+5.2%", logo: "L", tokenId: "liqf" },
    { name: "RavoToken", symbol: "RAVO", price: "$0.67", change: "+22.1%", logo: "R", tokenId: "ravo" }
  ];

  const handleTokenClick = (tokenId) => {
    navigate(`/swap?tokenId=${tokenId}`);
  };

  const handleViewMore = () => {
            navigate('/track');
  };

  return (
    <div className="trending-tokens-section">
      <div className="carousel-header">
        <h3 className="section-title">
          <FiTrendingUp />
          Trending Tokens
        </h3>
        <div className="carousel-controls">
          <div className="carousel-nav">
            <button className="nav-btn">
              <FiChevronLeft />
            </button>
            <button className="nav-btn">
              <FiChevronRight />
            </button>
          </div>
          <button className="view-more-btn" onClick={handleViewMore}>
            View More <FiArrowRight />
          </button>
        </div>
      </div>
      
      <div className="trending-tokens">
        {trendingTokens.map((token, index) => (
          <div 
            key={index} 
            className="trending-token"
            onClick={() => handleTokenClick(token.tokenId)}
            style={{ cursor: 'pointer' }}
          >
            <div className="trending-token-logo">
              {token.logo}
            </div>
            <div className="trending-token-info">
              <div className="trending-token-name">
                {token.name}
                <span className="trending-token-symbol"> {token.symbol}</span>
              </div>
              <div className="trending-token-price">
                {token.price}
                <span className={`price-change ${token.change.startsWith('+') ? 'positive' : 'negative'}`}>
                  {token.change}
                </span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default TrendingTokens; 