// frontend/src/pages/SwapPageDesktop.js

import React, { useState, useEffect, useRef } from 'react';
import { FiCopy, FiCheck, FiSun, FiMoon, FiMessageCircle, FiGlobe, FiTwitter, FiBarChart, FiDollarSign, FiTarget, FiRepeat, FiChevronDown, FiRefreshCw, FiSettings, FiSend } from 'react-icons/fi';
import { createChart } from 'lightweight-charts';
import './SwapPageDesktop.css';

const MOCK_HOLDERS = [
  { rank: 1, address: '0x89205A....3b2A69', percent: '18.2%' },
  { rank: 2, address: '0x7Be807....D12b', percent: '14.7%' },
  { rank: 3, address: '0x34aA3F....fDF3', percent: '12.3%' },
  { rank: 4, address: '0x5aAeb6....BeAed', percent: '9.6%' },
  { rank: 5, address: '0xfB6916....c5d359', percent: '7.8%' },
  { rank: 6, address: '0x5fD2B5....3f0f5e', percent: '5.4%' },
  { rank: 7, address: '0x742d35....8f44e', percent: '4.2%' },
];

const MOCK_TOKEN = {
  name: 'Ethereum',
  symbol: 'ETH',
  description: 'Ethereum is a decentralized, open-source blockchain with smart contract functionality. Ether is the native cryptocurrency of the platform. Among cryptocurrencies, Ether is second only to Bitcoin in market capitalization. Ethereum was conceived in 2013 by programmer Vitalik Buterin.',
  contractAddress: '0x742d35Cc6634C0532925a3b844Bc454e4438f44e',
  logo: 'https://cryptologos.cc/logos/ethereum-eth-logo.png',
  socials: {
    telegram: 'https://t.me/ethereum',
    website: 'https://ethereum.org',
    twitter: 'https://x.com/ethereum'
  }
};

const INITIAL_TRANSACTIONS = [
  { type: 'buy', time: '10:23:45', price: 3420.12, amount: 1.25, total: 4275.15 },
  { type: 'sell', time: '10:22:30', price: 3419.87, amount: 0.75, total: 2564.90 },
  { type: 'buy', time: '10:21:15', price: 3418.50, amount: 2.10, total: 7178.85 },
  { type: 'sell', time: '10:20:40', price: 3417.25, amount: 1.50, total: 5125.88 },
  { type: 'buy', time: '10:19:55', price: 3416.80, amount: 0.45, total: 1537.56 },
  { type: 'sell', time: '10:19:20', price: 3415.60, amount: 0.85, total: 2903.26 },
  { type: 'buy', time: '10:18:45', price: 3414.90, amount: 1.20, total: 4097.88 },
];

const INITIAL_CHAT = [
  { type: 'system', text: 'Welcome to the ETH/TOKEN swap community chat!' },
  { type: 'system', text: 'System: Liquidity pool updated - 100.56 ETH now available' },
  { type: 'user', text: 'Just swapped 1 ETH for TOKEN. Smooth process!' },
  { type: 'system', text: 'User123 joined the chat' },
  { type: 'user', text: "What's the current APY for providing liquidity?" },
  { type: 'system', text: 'Mod: Current APY is 12.5% for ETH/TOKEN pair' }
];

function getTimeString() {
  const now = new Date();
  return `${now.getHours().toString().padStart(2, '0')}:${now.getMinutes().toString().padStart(2, '0')}:${now.getSeconds().toString().padStart(2, '0')}`;
}

function isMobile() {
  return window.innerWidth < 1024;
}

const SwapPageDesktop = ({ isDarkMode, onToggleTheme }) => {
  // Theme
  const [isLight, setIsLight] = useState(!isDarkMode);

  // Token info
  const [expandedDescription, setExpandedDescription] = useState(false);
  const [copiedAddress, setCopiedAddress] = useState(false);

  // Metrics & Progress
  const [bondingProgress, setBondingProgress] = useState(50);
  const [bondingChange, setBondingChange] = useState({ up: true, value: 2.3 });
  const [ethInPool, setEthInPool] = useState(100.56);
  const [ethInPoolChange, setEthInPoolChange] = useState({ up: true, value: 3.2 });
  const [migrationThreshold, setMigrationThreshold] = useState(50);
  const [migrationChange, setMigrationChange] = useState({ up: false, value: 0.5 });
  const [migrationRemaining, setMigrationRemaining] = useState(49.44);
  const [progressCurrent, setProgressCurrent] = useState(3420);

  // Chart
  const chartRef = useRef(null);
  const chartInstanceRef = useRef(null);

  // Transactions
  const [transactions, setTransactions] = useState(INITIAL_TRANSACTIONS);

  // Chat
  const [showChat, setShowChat] = useState(false);
  const [chatMessages, setChatMessages] = useState(INITIAL_CHAT);
  const [newMessage, setNewMessage] = useState('');

  // Swap
  const [payAmount, setPayAmount] = useState('1.0');
  const [receiveAmount, setReceiveAmount] = useState('1500.00');
  const [payToken, setPayToken] = useState('ETH');
  const [receiveToken, setReceiveToken] = useState('TOKEN');
  const payTokenBalance = 2.54;

  // Slippage
  const [showSlippage, setShowSlippage] = useState(false);
  const [selectedSlippage, setSelectedSlippage] = useState(0.5);
  const [customSlippage, setCustomSlippage] = useState(0.5);

  // Copy contract address
  const copyContractAddress = async () => {
    try {
      await navigator.clipboard.writeText(MOCK_TOKEN.contractAddress);
      setCopiedAddress(true);
      setTimeout(() => setCopiedAddress(false), 2000);
    } catch (err) {
      // ignore
    }
  };

  // Theme toggle - sync with global theme
  useEffect(() => {
    setIsLight(!isDarkMode);
  }, [isDarkMode]);

  useEffect(() => {
    if (isLight) {
      document.body.classList.add('light-theme');
    } else {
      document.body.classList.remove('light-theme');
    }
    // Re-init chart on theme change
    setTimeout(() => {
      initChart();
    }, 0);
    // eslint-disable-next-line
  }, [isLight]);

  // Chart
  function initChart() {
    if (!chartRef.current || isMobile()) return;
    
    // Additional safety check
    if (!chartRef.current.clientWidth || chartRef.current.clientWidth === 0) {
      console.log('Chart container not ready, retrying...');
      setTimeout(() => initChart(), 100);
      return;
    }
    
    chartRef.current.innerHTML = '';
    if (chartInstanceRef.current) {
      chartInstanceRef.current.remove();
      chartInstanceRef.current = null;
    }
    
    try {
      const chart = createChart(chartRef.current, {
        width: chartRef.current.clientWidth,
        height: 350,
        layout: {
          backgroundColor: 'transparent',
          textColor: isLight ? '#0f172a' : '#cbd5e1',
        },
        grid: {
          vertLines: {
            color: isLight ? 'rgba(0, 0, 0, 0.05)' : 'rgba(255, 255, 255, 0.05)',
          },
          horzLines: {
            color: isLight ? 'rgba(0, 0, 0, 0.05)' : 'rgba(255, 255, 255, 0.05)',
          },
        },
        timeScale: {
          timeVisible: true,
          secondsVisible: false,
        },
      });
      
      const candleSeries = chart.addCandlestickSeries({
        upColor: '#10B981',
        downColor: '#EF4444',
        borderDownColor: '#EF4444',
        borderUpColor: '#10B981',
        wickDownColor: '#EF4444',
        wickUpColor: '#10B981',
      });
      
      // Generate mock data
      const data = [];
      let value = 3400;
      const now = new Date();
      for (let i = 0; i < 100; i++) {
        const time = new Date(now);
        time.setDate(time.getDate() - (99 - i));
        const open = value;
        const close = open + (Math.random() - 0.5) * 80;
        const high = Math.max(open, close) + Math.random() * 40;
        const low = Math.min(open, close) - Math.random() * 40;
        data.push({
          time: Math.floor(time.getTime() / 1000),
          open,
          high,
          low,
          close,
        });
        value = close;
      }
      candleSeries.setData(data);
      chartInstanceRef.current = chart;
      
      // Safe resize handler
      function handleResize() {
        if (chartRef.current && chartInstanceRef.current && chartRef.current.clientWidth > 0) {
          chartInstanceRef.current.applyOptions({ width: chartRef.current.clientWidth });
        }
      }
      
      window.addEventListener('resize', handleResize);
      
      return () => {
        window.removeEventListener('resize', handleResize);
        if (chartInstanceRef.current) {
          chartInstanceRef.current.remove();
          chartInstanceRef.current = null;
        }
      };
    } catch (error) {
      console.error('Chart initialization failed:', error);
      return () => {};
    }
  }

  useEffect(() => {
    if (!isMobile()) {
      // Delay chart initialization to ensure DOM is ready
      const timer = setTimeout(() => {
        const cleanup = initChart();
        return cleanup;
      }, 100);
      
      return () => {
        clearTimeout(timer);
        if (chartInstanceRef.current) {
          chartInstanceRef.current.remove();
          chartInstanceRef.current = null;
        }
      };
    }
    return () => {
      if (chartInstanceRef.current) {
        chartInstanceRef.current.remove();
        chartInstanceRef.current = null;
      }
    };
  }, [isLight]);

  // Live metrics & progress bar
  useEffect(() => {
    const interval = setInterval(() => {
      // Animate metrics
      setBondingChange({ up: Math.random() > 0.3, value: +(Math.random() * 2).toFixed(2) });
      setEthInPoolChange({ up: Math.random() > 0.3, value: +(Math.random() * 2).toFixed(2) });
      setMigrationChange({ up: Math.random() > 0.7, value: +(Math.random() * 1).toFixed(2) });

      // Animate progress bar
      setBondingProgress(prev => {
        let next = prev + (Math.random() - 0.5) * 5;
        if (next < 0) next = 0;
        if (next > 100) next = 100;
        return +next.toFixed(1);
      });
      setProgressCurrent(+(3400 + (Math.random() - 0.5) * 100).toFixed(2));
    }, 3000);
    return () => clearInterval(interval);
  }, []);

  // Live transactions
  useEffect(() => {
    const interval = setInterval(() => {
      if (Math.random() > 0.5) {
        const isBuy = Math.random() > 0.5;
        const price = +(3400 + (Math.random() - 0.5) * 20).toFixed(2);
        const amount = +(Math.random() * 2).toFixed(2);
        const total = +(price * amount).toFixed(2);
        const time = getTimeString();
        setTransactions(prev => {
          const next = [{ type: isBuy ? 'buy' : 'sell', time, price, amount, total }, ...prev];
          return next.slice(0, 10);
        });
      }
    }, 3000);
    return () => clearInterval(interval);
  }, []);

  // Flip tokens
  const flipTokens = () => {
    setPayAmount(receiveAmount);
    setReceiveAmount(payAmount);
    setPayToken(receiveToken);
    setReceiveToken(payToken);
  };

  // Slippage
  const handleSlippageSelect = (value) => {
    setSelectedSlippage(value);
    setCustomSlippage(value);
  };
  const confirmCustomSlippage = () => {
    setSelectedSlippage(customSlippage);
    setShowSlippage(false);
  };

  // Chat
  const sendMessage = () => {
    if (newMessage.trim()) {
      setChatMessages(prev => [...prev, { type: 'user', text: newMessage.trim() }]);
      setNewMessage('');
      setTimeout(() => {
        const responses = [
          "Thanks for your message!",
          "That's an interesting point.",
          "I'll look into that.",
          "The team is working on that feature.",
          "You can find more info in our docs."
        ];
        const randomResponse = responses[Math.floor(Math.random() * responses.length)];
        setChatMessages(prev => [...prev, { type: 'system', text: randomResponse }]);
      }, 1000);
    }
  };
  const handleKeyPress = (e) => {
    if (e.key === 'Enter') sendMessage();
  };

  // Percentage buttons
  const handlePercentageClick = (percentage) => {
    const balance = payTokenBalance;
    let amount;
    switch (percentage) {
      case 25: amount = (balance * 0.25).toFixed(2); break;
      case 50: amount = (balance * 0.5).toFixed(2); break;
      case 75: amount = (balance * 0.75).toFixed(2); break;
      case 'max': amount = balance.toFixed(2); break;
      default: amount = balance.toFixed(2);
    }
    setPayAmount(amount);
  };

  // Render
  return (
    <div className={`swap-page-desktop${isLight ? ' light' : ' dark'}`}>
      <button className="theme-toggle" onClick={onToggleTheme}>
        {isLight ? <FiSun /> : <FiMoon />}
        {isLight ? 'Light Mode' : 'Dark Mode'}
      </button>
      <div className="container">
        <div className="main-content">
          {/* Token Information */}
          <div className="card">
            <div className="token-info">
              <div className="token-logo">
                <img src={MOCK_TOKEN.logo} alt={MOCK_TOKEN.name} />
              </div>
              <div className="token-details">
                <div className="token-name">
                  <h2>{MOCK_TOKEN.name}</h2>
                  <span className="token-symbol">{MOCK_TOKEN.symbol}</span>
                  <div className="status-badge">
                    <FiCheck /> Active Trading
                  </div>
                </div>
                <div className={`token-description${expandedDescription ? ' expanded' : ''}`}>
                  {MOCK_TOKEN.description}
                  <button className="see-more" onClick={() => setExpandedDescription(e => !e)}>
                    {expandedDescription ? 'See less' : 'See more'}
                  </button>
                </div>
                <div className={`contract-address${copiedAddress ? ' copied' : ''}`}>
                  <span>{MOCK_TOKEN.contractAddress}</span>
                  <button className="copy-btn" onClick={copyContractAddress}>
                    {copiedAddress ? <FiCheck /> : <FiCopy />}
                  </button>
                </div>
                <div className="social-links">
                  <button className="social-btn" onClick={() => window.open(MOCK_TOKEN.socials.telegram, '_blank')}><FiMessageCircle /></button>
                  <button className="social-btn" onClick={() => window.open(MOCK_TOKEN.socials.website, '_blank')}><FiGlobe /></button>
                  <button className="social-btn" onClick={() => window.open(MOCK_TOKEN.socials.twitter, '_blank')}><FiTwitter /></button>
                </div>
              </div>
            </div>
          </div>
          {/* Metrics & Progress */}
          <div className="card">
            <div className="metrics-grid">
              <div className="metric-card">
                <div className="metric-header">
                  <div className="metric-icon"><FiBarChart /></div>
                  <div className="metric-title">Bonding Curve Progress</div>
                </div>
                <div className="metric-value">
                  {bondingProgress.toFixed(0)}% <span className={`metric-change ${bondingChange.up ? 'change-up' : 'change-down'}`}>{bondingChange.up ? '' : ''} {bondingChange.value}%</span>
                </div>
              </div>
              <div className="metric-card">
                <div className="metric-header">
                  <div className="metric-icon"><FiDollarSign /></div>
                  <div className="metric-title">Total ETH in Pool</div>
                </div>
                <div className="metric-value">
                  {ethInPool.toFixed(2)} ETH <span className={`metric-change ${ethInPoolChange.up ? 'change-up' : 'change-down'}`}>{ethInPoolChange.up ? '' : ''} {ethInPoolChange.value}%</span>
                </div>
                <div className="metric-subtext">24h change</div>
              </div>
              <div className="metric-card">
                <div className="metric-header">
                  <div className="metric-icon"><FiTarget /></div>
                  <div className="metric-title">Migration Threshold</div>
                </div>
                <div className="metric-value">
                  {migrationThreshold} ETH <span className={`metric-change ${migrationChange.up ? 'change-up' : 'change-down'}`}>{migrationChange.up ? '' : ''} {migrationChange.value}%</span>
                </div>
                <div className="metric-subtext">Remaining: {migrationRemaining} ETH</div>
              </div>
            </div>
            <div className="progress-container">
              <div className="progress-fill" style={{ width: `${bondingProgress}%` }}></div>
            </div>
            <div className="progress-labels">
              <span>Min: $3,000</span>
              <span>Max: $4,000</span>
            </div>
            <div className="progress-info">
              <span>{bondingProgress.toFixed(0)}% Complete</span>
              <span>Current: ${progressCurrent}</span>
            </div>
          </div>
          {/* Market Data */}
          <div className="card">
            {!isMobile() && (
              <div className="chart-header">
                <h3>ETH/USD Price Chart</h3>
                <div className="time-filters">
                  <button className="time-filter">1M</button>
                  <button className="time-filter">5M</button>
                  <button className="time-filter">15M</button>
                  <button className="time-filter">1H</button>
                  <button className="time-filter">1D</button>
                  <button className="time-filter active">1W</button>
                  <button className="time-filter">1M</button>
                </div>
              </div>
            )}
            {!isMobile() && <div className="chart-container" ref={chartRef}></div>}
            <div className="transactions-header">
              <h3>Live Transactions</h3>
              <button className="toggle-chart-btn" onClick={() => setShowChat(s => !s)}>
                {showChat ? <FiRepeat /> : <FiMessageCircle />}
                {showChat ? ' Show Transactions' : ' Show Chat'}
              </button>
            </div>
            {!showChat ? (
              <div className="order-book">
                <table>
                  <thead>
                    <tr>
                      <th>Time</th>
                      <th>Price (USD)</th>
                      <th>Amount (ETH)</th>
                      <th>Total (USD)</th>
                    </tr>
                  </thead>
                  <tbody>
                    {transactions.map((tx, i) => (
                      <tr key={i} className={tx.type === 'buy' ? 'buy-row' : 'sell-row'}>
                        <td>{tx.time}</td>
                        <td>{tx.price}</td>
                        <td>{tx.amount}</td>
                        <td>{tx.total}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            ) : (
              <div className="chat-section">
                <div className="chat-container" id="chatContainer">
                  {chatMessages.map((msg, i) => (
                    <div key={i} className={`message ${msg.type === 'user' ? 'user-message' : 'system-message'}`}>{msg.text}</div>
                  ))}
                </div>
                <div className="chat-input">
                  <input
                    type="text"
                    placeholder="Type a message..."
                    value={newMessage}
                    onChange={e => setNewMessage(e.target.value)}
                    onKeyPress={handleKeyPress}
                  />
                  <button className="send-btn" onClick={sendMessage}><FiSend /></button>
                </div>
              </div>
            )}
          </div>
        </div>
        <div className="sidebar">
          {/* Swap Interface */}
          <div className="card">
            <div className="swap-header">
              <h2 className="swap-title">Swap</h2>
              <div className="swap-actions">
                <button className="swap-icon-btn"><FiRefreshCw /></button>
                <button className="swap-icon-btn" onClick={() => setShowSlippage(true)}><FiSettings /></button>
              </div>
            </div>
            <div className="swap-fields">
              <div className="swap-section">
                <div className="section-header">
                  <span>You Pay</span>
                  <span>Balance: {payTokenBalance} ETH</span>
                </div>
                <div className="token-input">
                  <input
                    type="text"
                    className="amount-input"
                    value={payAmount}
                    onChange={e => setPayAmount(e.target.value)}
                  />
                  <button className="token-selector">
                    <FiDollarSign /> {payToken} <FiChevronDown />
                  </button>
                </div>
                <div className="percentage-buttons">
                  <button className="percentage-btn" onClick={() => handlePercentageClick(25)}>25%</button>
                  <button className="percentage-btn" onClick={() => handlePercentageClick(50)}>50%</button>
                  <button className="percentage-btn" onClick={() => handlePercentageClick(75)}>75%</button>
                  <button className="percentage-btn" onClick={() => handlePercentageClick('max')}>Max</button>
                </div>
              </div>
              <div className="flip-button-container">
                <button className="flip-button" onClick={flipTokens}><FiRepeat /></button>
              </div>
              <div className="swap-section">
                <div className="section-header">
                  <span>You Receive</span>
                  <span>Balance: 1,500.00 TOKEN</span>
                </div>
                <div className="token-input">
                  <input
                    type="text"
                    className="amount-input"
                    value={receiveAmount}
                    readOnly
                  />
                  <button className="token-selector">
                    <FiDollarSign /> {receiveToken} <FiChevronDown />
                  </button>
                </div>
              </div>
            </div>
            <div className="swap-info">
              <div className="info-row">
                <span className="info-label">Price</span>
                <span className="info-value">1 ETH = 1,500.00 TOKEN</span>
              </div>
              <div className="info-row">
                <span className="info-label">Price Impact</span>
                <span className="info-value price-impact">0.15%</span>
              </div>
              <div className="info-row">
                <span className="info-label">Network Fee</span>
                <span className="info-value">0.005 ETH</span>
              </div>
              <div className="info-row">
                <span className="info-label">Minimum Received</span>
                <span className="info-value">1,492.50 TOKEN</span>
              </div>
            </div>
            <button className="swap-btn">Swap</button>
          </div>
          {/* Holders Section */}
          <div className="card">
            <h3 style={{ marginBottom: '15px' }}>Top Token Holders</h3>
            <div className="holders-list">
              {MOCK_HOLDERS.map((h, i) => (
                <div className="holder-item" key={i}>
                  <div className={`holder-rank rank-${h.rank <= 3 ? h.rank : '4-7'}`}>{h.rank}</div>
                  <div className="holder-address">{h.address}</div>
                  <div className="holder-percentage">{h.percent}</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      {/* Slippage Modal */}
      {showSlippage && (
        <div className="modal-overlay" onClick={() => setShowSlippage(false)}>
          <div className="modal" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h3 className="modal-title">Slippage Settings</h3>
              <button className="close-modal" onClick={() => setShowSlippage(false)}>&times;</button>
            </div>
            <p style={{ marginBottom: '15px', color: 'var(--text-secondary)' }}>
              Your transaction will revert if the price changes unfavorably by more than this percentage.
            </p>
            <div className="slippage-options">
              <button className={`slippage-option${selectedSlippage === 0.1 ? ' active' : ''}`} onClick={() => handleSlippageSelect(0.1)}>0.1%</button>
              <button className={`slippage-option${selectedSlippage === 0.5 ? ' active' : ''}`} onClick={() => handleSlippageSelect(0.5)}>0.5%</button>
              <button className={`slippage-option${selectedSlippage === 1 ? ' active' : ''}`} onClick={() => handleSlippageSelect(1)}>1%</button>
              <button className={`slippage-option${selectedSlippage === 2 ? ' active' : ''}`} onClick={() => handleSlippageSelect(2)}>2%</button>
            </div>
            <div className="custom-slippage">
              <input
                type="number"
                placeholder="Custom"
                value={customSlippage}
                onChange={e => setCustomSlippage(parseFloat(e.target.value) || 0)}
              />
              <span className="percent-symbol">%</span>
            </div>
            <button className="confirm-btn" onClick={confirmCustomSlippage}>Confirm</button>
          </div>
        </div>
      )}
      <div className="unsupported-message">
        This interface is optimized for desktop use only. Please access from a device with a screen width of 1024px or larger.
      </div>
    </div>
  );
};

export default SwapPageDesktop;