import React, { useState, useEffect } from 'react';
import TokenCard from './TokenCard';
import { useNavigate } from 'react-router-dom';
import './NewTokenCardsUpdated.css';

const NewTokenCardsUpdated = ({ activeFilter, isDarkMode, searchQuery = '' }) => {
  const navigate = useNavigate();
  
  const [currentPage, setCurrentPage] = useState(1);
  const tokensPerPage = 12; // Reduced for better performance with new cards

  // Generate mock tokens with the new TokenCard format
  const generateMockTokens = () => {
    const mockTokens = [];
    const tokenNames = [
      "BaseToken", "MyTime", "BABYS", "TestToken", "CryptoCoin", "DeFiToken", "Blockchain", "SmartContract",
      "Ethereum", "Bitcoin", "Litecoin", "Ripple", "Cardano", "Polkadot", "Chainlink", "Uniswap",
      "Aave", "Compound", "Maker", "SushiSwap", "PancakeSwap", "Curve", "Yearn", "Balancer",
      "Synthetix", "Ren", "Loopring", "Optimism", "Arbitrum", "Polygon", "Avalanche", "Solana",
      "Cosmos", "Terra", "Binance", "Tron", "EOS", "Tezos", "Algorand", "Stellar", "VeChain",
      "IOTA", "NEO", "Qtum", "Waves", "Zilliqa", "Ontology", "ICON", "Aion", "Wanchain",
      "Aeternity", "Elastos", "Neblio", "Pivx", "Navcoin", "Komodo", "Decred", "Zcash", "Monero"
    ];

    for (let i = 1; i <= 65; i++) {
      const name = tokenNames[i - 1] || `Token${i}`;
      const symbol = name.substring(0, 4).toUpperCase();
      const marketCap = Math.floor(Math.random() * 10000000) + 1000;
      const volume24h = Math.floor(Math.random() * 1000000) + 100000;
      const migrationThreshold = (Math.random() * 50 + 5).toFixed(1);
      const progress = Math.floor(Math.random() * 100) + 1;
      
      // Determine status based on progress
      let status = 'new';
      if (progress === 100) status = 'migrated';
      else if (progress > 50) status = 'migrating';
      
      // Generate time remaining based on status
      let timeRemaining = '1d 5h';
      if (status === 'migrated') timeRemaining = 'Completed';
      else if (status === 'migrating') timeRemaining = `${Math.floor(Math.random() * 3) + 1}d ${Math.floor(Math.random() * 24)}h`;
      else timeRemaining = `${Math.floor(Math.random() * 7) + 1}d ${Math.floor(Math.random() * 24)}h`;
      
      // Generate a mock Ethereum address
      const mockAddress = `0x${Math.random().toString(16).substring(2, 42).padEnd(40, '0')}`;
      const shortAddress = `${mockAddress.substring(0, 6)}...${mockAddress.substring(38)}`;

      // Generate social links
      const hasWebsite = Math.random() > 0.3;
      const hasTelegram = Math.random() > 0.2;
      const hasTwitter = Math.random() > 0.2;

      mockTokens.push({
        id: i,
        logo: name.charAt(0),
        name: name,
        symbol: `${symbol}  ${Math.random() > 0.5 ? 'Ethereum' : 'Base'}`,
        contractAddress: shortAddress,
        status: status,
        marketCap: `$${(marketCap / 1000000).toFixed(1)}M`,
        volume24h: `$${(volume24h / 1000000).toFixed(1)}M`,
        migrationThreshold: `${migrationThreshold} ETH`,
        timeRemaining: timeRemaining,
        progress: progress,
        socials: {
          website: hasWebsite ? `https://${name.toLowerCase()}.com` : '#',
          telegram: hasTelegram ? `https://t.me/${name.toLowerCase()}` : '#',
          twitter: hasTwitter ? `https://twitter.com/${name.toLowerCase()}` : '#'
        }
      });
    }
    return mockTokens;
  };

  const tokens = generateMockTokens();

  // Search function to filter tokens by name, symbol, or address
  const searchTokens = (tokens, query) => {
    if (!query.trim()) return tokens;
    
    const searchTerm = query.toLowerCase().trim();
    return tokens.filter(token => 
      token.name.toLowerCase().includes(searchTerm) ||
      token.symbol.toLowerCase().includes(searchTerm) ||
      token.contractAddress.toLowerCase().includes(searchTerm)
    );
  };

  // Filter tokens based on activeFilter and search query
  const filteredTokens = tokens.filter(token => {
    if (activeFilter === 'all') return true;
    if (activeFilter === 'migrated') return token.status === 'migrated';
    if (activeFilter === 'upcoming') return token.progress < 100;
    if (activeFilter === 'new') return token.status === 'new';
    return true;
  });

  const searchedTokens = searchTokens(filteredTokens, searchQuery);
  const totalPages = Math.ceil(searchedTokens.length / tokensPerPage);
  const startIndex = (currentPage - 1) * tokensPerPage;
  const endIndex = startIndex + tokensPerPage;
  const currentTokens = searchedTokens.slice(startIndex, endIndex);

  const handleCardClick = (tokenId) => {
    // Navigate to token details or swap page
    navigate(`/swap?token=${tokenId}`);
  };

  const handlePreviousPage = () => {
    setCurrentPage(prev => Math.max(prev - 1, 1));
  };

  const handleNextPage = () => {
    setCurrentPage(prev => Math.min(prev + 1, totalPages));
  };

  // Reset to first page when filter or search changes
  useEffect(() => {
    setCurrentPage(1);
  }, [activeFilter, searchQuery]);

  return (
    <div className="new-token-cards-container">
      {/* Token Cards Grid */}
      <div className="token-cards-grid">
        {currentTokens.map((token) => (
          <div key={token.id} className="token-card-wrapper" onClick={() => handleCardClick(token.id)}>
            <TokenCard token={token} />
          </div>
        ))}
      </div>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="pagination-container">
          <button 
            onClick={handlePreviousPage} 
            disabled={currentPage === 1}
            className="pagination-btn"
          >
            Previous
          </button>
          <span className="pagination-info">
            Page {currentPage} of {totalPages}
          </span>
          <button 
            onClick={handleNextPage} 
            disabled={currentPage === totalPages}
            className="pagination-btn"
          >
            Next
          </button>
        </div>
      )}

      {/* No results message */}
      {searchedTokens.length === 0 && (
        <div className="no-results">
          <p>No tokens found matching your criteria.</p>
        </div>
      )}
    </div>
  );
};

export default NewTokenCardsUpdated; 