import React, { useState } from 'react';
import { 
  FiHome, 
  FiPlus, 
  FiSettings, 
  FiCreditCard, 
  FiTrendingUp,
  FiChevronUp,
  FiChevronDown
} from 'react-icons/fi';

const FooterNav = ({ currentPage, onNavigate }) => {
  const [isFooterVisible, setIsFooterVisible] = useState(true);

  const navItems = [
    { id: 'trending', label: 'Track', icon: FiTrendingUp },
    { id: 'create-token', label: 'Create Token', icon: FiPlus },
    { id: 'home', label: 'Home', icon: FiHome },
    { id: 'manage-token', label: 'Manage Token', icon: FiSettings },
    { id: 'wallet', label: 'My Wallet', icon: FiCreditCard }
  ];

  const handleNavClick = (itemId) => {
    onNavigate(itemId);
  };

  const toggleFooter = () => {
    setIsFooterVisible(!isFooterVisible);
  };

  return (
    <>
      <footer className={`footer ${!isFooterVisible ? 'hidden' : ''}`}>
        <div className="footer-nav">
          {navItems.map((item) => {
            const isActive = currentPage === item.id;
            return (
              <div
                key={item.id}
                onClick={() => handleNavClick(item.id)}
                className={`nav-item ${isActive ? 'active' : ''}`}
              >
                <item.icon />
                <span>{item.label}</span>
              </div>
            );
          })}
        </div>
      </footer>
      <button className="footer-toggle" onClick={toggleFooter}>
        {isFooterVisible ? (
          <FiChevronDown className="arrow-down" size={20} />
        ) : (
          <FiChevronUp className="arrow-up" size={20} />
        )}
      </button>
    </>
  );
};

export default FooterNav;