import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import Dashboard from './components/Dashboard';
import CreateToken from './pages/CreateToken';
import ManageToken from './pages/ManageToken';
import Wallet from './pages/Wallet';
import TrendingTokensUpdated from './pages/TrendingTokensUpdated';
import SwapPage from './pages/SwapPage';
import SwapPagePc from './pages/SwapPagePc';
import SwapPageDesktop from './pages/SwapPageDesktop';
import TokenCardTest from './pages/TokenCardTest';
import ModernHeader from './components/ModernHeader';
import FooterNav from './components/FooterNav';
import './index.css';

function AppContent() {
  const [currentPage, setCurrentPage] = useState('home');
  const [isDarkMode, setIsDarkMode] = useState(true);
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);
  const [searchQuery, setSearchQuery] = useState('');

  const navigate = useNavigate();
  const location = useLocation();

  // Handle window resize
  useEffect(() => {
    const handleResize = () => {
      setScreenWidth(window.innerWidth);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    const path = location.pathname;
    if (path === '/') {
      setCurrentPage('home');
    } else if (path === '/create-token') {
      setCurrentPage('create-token');
    } else if (path === '/manage-token') {
      setCurrentPage('manage-token');
    } else if (path === '/wallet') {
      setCurrentPage('wallet');
    } else if (path === '/track') {
      setCurrentPage('trending');
    } else if (path.startsWith('/swap')) {
      setCurrentPage('swap');
    }
  }, [location]);

  const handleNavigation = (page) => {
    setCurrentPage(page);
    switch (page) {
      case 'home': navigate('/'); break;
      case 'create-token': navigate('/create-token'); break;
      case 'manage-token': navigate('/manage-token'); break;
      case 'wallet': navigate('/wallet'); break;
      case 'trending': navigate('/track'); break;
      default: navigate('/');
    }
  };

  const toggleTheme = () => {
    setIsDarkMode(!isDarkMode);
  };

  const handleSearch = (query) => {
    setSearchQuery(query);
  };

  // Determine which swap page to render based on screen width
  const SwapPageComponent = screenWidth >= 1024 ? SwapPageDesktop : SwapPage;

  return (
    <div className={`App ${isDarkMode ? 'dark-mode' : 'light-mode'}`}>
      <ModernHeader
        isDarkMode={isDarkMode}
        onToggleTheme={toggleTheme}
        currentPage={currentPage}
        onNavigate={handleNavigation}
        searchQuery={searchQuery}
        onSearch={handleSearch}
      />
      <main className="container">
        <Routes>
          <Route path="/" element={
            <Dashboard
              isDarkMode={isDarkMode}
              onToggleTheme={toggleTheme}
              currentPage={currentPage}
              onNavigate={handleNavigation}
              searchQuery={searchQuery}
            />
          } />
          <Route path="/create-token" element={<CreateToken />} />
          <Route path="/manage-token" element={<ManageToken />} />
          <Route path="/wallet" element={
            <Wallet
              currentPage={currentPage}
              onNavigate={handleNavigation}
            />
          } />
          <Route path="/track" element={
            <TrendingTokensUpdated
              isDarkMode={isDarkMode}
              onToggleTheme={toggleTheme}
              currentPage={currentPage}
              onNavigate={handleNavigation}
            />
          } />
          <Route path="/swap" element={
            <SwapPageComponent
              isDarkMode={isDarkMode}
              onToggleTheme={toggleTheme}
            />
          } />
          <Route path="/token-card-test" element={<TokenCardTest />} />
        </Routes>
      </main>
      <FooterNav
        currentPage={currentPage}
        onNavigate={handleNavigation}
        isDarkMode={isDarkMode}
      />
      {/* Copy Notification */}
      <div id="copyNotification" className="copy-notification">
        Copied to clipboard!
      </div>
    </div>
  );
}

function App() {
  return (
    <Router>
      <AppContent />
    </Router>
  );
}

export default App; 