  const [searchParams] = useSearchParams();
  const tokenId = searchParams.get('token');

  // State management
  const [copiedAddress, setCopiedAddress] = useState(false);
  const [activeTab, setActiveTab] = useState('swap');
  const [chatMessages, setChatMessages] = useState([
    { type: 'system', text: 'Welcome to the trading community!' },
    { type: 'user', text: 'Anyone knows why the price is pumping today?' },
    { type: 'system', text: 'The price increase is likely due to recent news.' }
  ]);
  const [newMessage, setNewMessage] = useState('');
  const [showSlippage, setShowSlippage] = useState(false);
  const [selectedSlippage, setSelectedSlippage] = useState(10);
  const [customSlippage, setCustomSlippage] = useState(10);
  const [payAmount, setPayAmount] = useState('1.0');
  const [receiveAmount, setReceiveAmount] = useState('1500.0');
  const [payToken, setPayToken] = useState('ETH');
  const [receiveToken, setReceiveToken] = useState('TOKEN');

