            { id: 'swap', label: 'Swap', icon: <FiArrowUp /> },
            { id: 'transactions', label: 'Transactions', icon: <FiArrowDown /> },
            { id: 'chat', label: 'Chat', icon: <FiMessageCircle /> },
            { id: 'holders', label: 'Holders', icon: <FiUsers /> }
