import React, { useState, useEffect } from 'react';
import { 
  FiInfo, 
  FiZap, 
  FiCalendar, 
  FiClock,
  FiDollarSign,
  FiAlertTriangle,
  FiShare2,
  FiAlignLeft,
  FiTrendingUp
} from 'react-icons/fi';

const CreateToken = ({ isDarkMode, toggleTheme, isWalletConnected, toggleWallet }) => {
  const [formData, setFormData] = useState({
    tokenName: 'BaseToken',
    tokenSymbol: 'BASE',
    telegram: 'https://t.me/basetoken',
    website: 'https://basetoken.io',
    twitter: 'https://twitter.com/basetoken',
    tokenImage: 'https://ipfs.io/ipfs/QmS9fN6tXU9bYh7h4cX3JxXvV7Lg2bR8bX8XkY7XfX6Xk',
    description: 'The official token of the Base ecosystem. Designed for long-term growth and community-driven development on the Base blockchain. This innovative token features a unique bonding curve mechanism that ensures fair distribution and sustainable growth.',
    migrationThreshold: '40.0',
    initialBuyAmount: '0.5',
    launchDate: '2023-10-15',
    launchHour: '20',
    launchMinute: '30',
    launchSecond: '30'
  });

  const [errors, setErrors] = useState({});
  const [instantLaunch, setInstantLaunch] = useState(false);
  const [ethPrice, setEthPrice] = useState(2600); // Mock ETH price
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Form validation
  const validateForm = () => {
    const newErrors = {};

    if (!formData.tokenName.trim()) {
      newErrors.tokenName = 'Token name is required';
    }

    if (!formData.tokenSymbol.trim()) {
      newErrors.tokenSymbol = 'Token symbol is required';
    } else if (formData.tokenSymbol.length < 3 || formData.tokenSymbol.length > 5) {
      newErrors.tokenSymbol = 'Token symbol must be 3-5 characters';
    }

    if (formData.description.length > 3000) {
      newErrors.description = 'Description must be 3000 characters or less';
    }

    if (parseFloat(formData.migrationThreshold) < 0.00019) {
      newErrors.migrationThreshold = 'Migration threshold must be at least 0.00019 ETH';
    }

    if (parseFloat(formData.initialBuyAmount) <= 0) {
      newErrors.initialBuyAmount = 'Initial buy amount must be greater than 0';
    }

    // URL validation
    const urlFields = ['telegram', 'website', 'twitter', 'tokenImage'];
    urlFields.forEach(field => {
      if (formData[field] && !isValidUrl(formData[field])) {
        newErrors[field] = 'Please enter a valid URL';
      }
    });

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const isValidUrl = (string) => {
    try {
      new URL(string);
      return true;
    } catch (_) {
      return false;
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleInstantLaunch = () => {
    setInstantLaunch(!instantLaunch);
    if (!instantLaunch) {
      // Set to current time for instant launch
      const now = new Date();
      setFormData(prev => ({
        ...prev,
        launchDate: now.toISOString().split('T')[0],
        launchHour: now.getHours().toString().padStart(2, '0'),
        launchMinute: now.getMinutes().toString().padStart(2, '0'),
        launchSecond: now.getSeconds().toString().padStart(2, '0')
      }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (validateForm()) {
      setIsSubmitting(true);
      try {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 2000));
      console.log('Form submitted:', formData);
        // Handle successful submission
        alert('Token created successfully!');
      } catch (error) {
        console.error('Error creating token:', error);
        alert('Error creating token. Please try again.');
      } finally {
        setIsSubmitting(false);
      }
    }
  };

  // Calculate costs
  const creationFee = 0.002;
  const initialBuy = parseFloat(formData.initialBuyAmount) || 0;
  const totalCost = creationFee + initialBuy;

  // Get current date for min date
  const today = new Date().toISOString().split('T')[0];

  // Format launch time preview
  const getLaunchTimePreview = () => {
    if (instantLaunch) {
      return 'Launch time: Instant (immediate trading)';
    }
    if (formData.launchDate) {
      const date = new Date(`${formData.launchDate}T${formData.launchHour}:${formData.launchMinute}:${formData.launchSecond}`);
      return `Launch time: ${date.toLocaleDateString('en-US', { 
        year: 'numeric', 
        month: 'long', 
        day: 'numeric' 
      })} at ${formData.launchHour}:${formData.launchMinute}:${formData.launchSecond} UTC`;
    }
    return 'Launch time: Not set';
  };

  return (
    <div className="container mx-auto px-4 pt-28 pb-64 flex justify-center">
      <div className="w-full max-w-4xl">
        <form onSubmit={handleSubmit} className="space-y-8 create-token-form">
        {/* Basic Information Section */}
        <div className="form-card">
          {/* Token Guide Box - Moved on top of Basic Information */}
          <div className="info-box mb-6" style={{ 
            background: 'linear-gradient(135deg, rgba(37, 99, 235, 0.08), rgba(14, 165, 233, 0.08))',
            border: '1px solid rgba(37, 99, 235, 0.15)',
            borderRadius: '10px',
            padding: '1rem',
            marginBottom: '1.5rem'
          }}>
            <div className="flex items-start space-x-3">
              <FiInfo className="text-blue-400 mt-1 flex-shrink-0" size={18} />
              <div>
                <h3 className="font-semibold mb-2 text-sm">Token Creation Guide</h3>
                <p className="text-xs text-gray-300 leading-relaxed">
                  The minimum migration threshold is 0.00019 ETH. For a successful token launch, consider setting an initial ETH amount to bootstrap liquidity and a reasonable migration threshold to ensure sufficient liquidity before migration to Uniswap.
                </p>
              </div>
            </div>
          </div>

          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiInfo className="text-blue-400" />
            Basic Information
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="form-label">Token Name *</label>
              <input
                type="text"
                name="tokenName"
                value={formData.tokenName}
                onChange={handleInputChange}
                className={`form-input ${errors.tokenName ? 'border-red-500' : ''}`}
                placeholder="e.g., My Token"
              />
              {errors.tokenName && <div className="form-error">{errors.tokenName}</div>}
              <div className="form-help">The full name of your token</div>
            </div>

            <div>
              <label className="form-label">Token Symbol *</label>
              <input
                type="text"
                name="tokenSymbol"
                value={formData.tokenSymbol}
                onChange={handleInputChange}
                className={`form-input ${errors.tokenSymbol ? 'border-red-500' : ''}`}
                placeholder="e.g., MTK"
                maxLength={5}
              />
              {errors.tokenSymbol && <div className="form-error">{errors.tokenSymbol}</div>}
              <div className="form-help">The symbol/ticker for your token (3-5 characters)</div>
            </div>
          </div>
        </div>

        {/* Social Links Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiShare2 className="text-blue-400" />
            Social Links
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="form-label">Telegram</label>
              <input
                type="url"
                name="telegram"
                value={formData.telegram}
                onChange={handleInputChange}
                className={`form-input ${errors.telegram ? 'border-red-500' : ''}`}
                placeholder="https://t.me/yourtoken"
              />
              {errors.telegram && <div className="form-error">{errors.telegram}</div>}
              <div className="form-help">Your Telegram group link</div>
            </div>

            <div>
              <label className="form-label">Website</label>
              <input
                type="url"
                name="website"
                value={formData.website}
                onChange={handleInputChange}
                className={`form-input ${errors.website ? 'border-red-500' : ''}`}
                placeholder="https://yourtoken.com"
              />
              {errors.website && <div className="form-error">{errors.website}</div>}
              <div className="form-help">Your project's website</div>
            </div>

            <div>
              <label className="form-label">X/Twitter Profile</label>
              <input
                type="url"
                name="twitter"
                value={formData.twitter}
                onChange={handleInputChange}
                className={`form-input ${errors.twitter ? 'border-red-500' : ''}`}
                placeholder="https://twitter.com/yourtoken"
              />
              {errors.twitter && <div className="form-error">{errors.twitter}</div>}
              <div className="form-help">Your X/Twitter profile link</div>
            </div>

            <div>
              <label className="form-label">Token Image URL</label>
              <input
                type="url"
                name="tokenImage"
                value={formData.tokenImage}
                onChange={handleInputChange}
                className={`form-input ${errors.tokenImage ? 'border-red-500' : ''}`}
                placeholder="https://yourtoken.com/logo.png"
              />
              {errors.tokenImage && <div className="form-error">{errors.tokenImage}</div>}
              <div className="form-help">URL for your token logo (PNG, 256x256 recommended)</div>
            </div>
          </div>
        </div>

        {/* Token Description Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiAlignLeft className="text-blue-400" />
            Token Description
          </h2>
          <div>
            <label className="form-label">Description</label>
            <textarea
              name="description"
              value={formData.description}
              onChange={handleInputChange}
              className={`form-input min-h-[120px] resize-none ${errors.description ? 'border-red-500' : ''}`}
              placeholder="Describe your token and project..."
              maxLength={3000}
            />
            {errors.description && <div className="form-error">{errors.description}</div>}
            <div className={`char-counter ${formData.description.length > 2800 ? 'warning' : ''} ${formData.description.length > 3000 ? 'error' : ''}`}>
              Max 3000 characters ({formData.description.length} used)
            </div>
          </div>
        </div>

        {/* Token Economics Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiTrendingUp className="text-blue-400" />
            Token Economics
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="form-label">Migration Threshold (ETH) *</label>
              <div className="relative">
              <input
                type="number"
                name="migrationThreshold"
                value={formData.migrationThreshold}
                onChange={handleInputChange}
                  className={`form-input pr-12 ${errors.migrationThreshold ? 'border-red-500' : ''}`}
                  placeholder="0.0"
                step="0.0001"
                min="0.00019"
              />
                <div className="absolute right-3 top-1/2 transform -translate-y-1/2 text-secondary font-medium">
                  ETH
                </div>
              </div>
              {errors.migrationThreshold && <div className="form-error">{errors.migrationThreshold}</div>}
              <div className="form-help">
                <FiAlertTriangle className="inline mr-1" size={14} />
                Minimum ETH required before migration to Uniswap (min: 0.00019 ETH)
              </div>
            </div>

            <div>
              <label className="form-label">Initial Buy Amount (ETH) *</label>
              <div className="relative">
              <input
                type="number"
                name="initialBuyAmount"
                value={formData.initialBuyAmount}
                onChange={handleInputChange}
                  className={`form-input pr-12 ${errors.initialBuyAmount ? 'border-red-500' : ''}`}
                  placeholder="0.0"
                  step="0.01"
                min="0.001"
              />
                <div className="absolute right-3 top-1/2 transform -translate-y-1/2 text-secondary font-medium">
                  ETH
                </div>
              </div>
              {errors.initialBuyAmount && <div className="form-error">{errors.initialBuyAmount}</div>}
              <div className="form-help">Initial ETH to buy tokens and bootstrap liquidity</div>
            </div>
          </div>
        </div>

        {/* Launch Settings Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiClock className="text-blue-400" />
            Launch Settings
          </h2>
          
          <div className="mb-6">
            <button
              type="button"
              onClick={handleInstantLaunch}
              className={`btn-instant w-full ${instantLaunch ? 'selected' : ''}`}
            >
              <FiZap className="inline mr-2" />
              {instantLaunch ? 'Instant Launch (Selected)' : 'Instant Launch'}
            </button>
            {instantLaunch && (
              <p className="text-sm text-green-400 mt-2">
                Token will launch immediately for trading
              </p>
            )}
          </div>

          <div className={`grid grid-cols-1 md:grid-cols-4 gap-4 ${instantLaunch ? 'opacity-50 pointer-events-none' : ''}`}>
            <div>
              <label className="form-label">Date</label>
              <input
                type="date"
                name="launchDate"
                value={formData.launchDate}
                onChange={handleInputChange}
                className="form-input"
                min={today}
              />
            </div>

            <div>
              <label className="form-label">Hour</label>
              <select
                name="launchHour"
                value={formData.launchHour}
                onChange={handleInputChange}
                className="form-input"
              >
                {Array.from({ length: 24 }, (_, i) => (
                  <option key={i} value={i.toString().padStart(2, '0')}>
                    {i.toString().padStart(2, '0')}
                  </option>
                ))}
              </select>
            </div>

            <div>
              <label className="form-label">Minute</label>
              <select
                name="launchMinute"
                value={formData.launchMinute}
                onChange={handleInputChange}
                className="form-input"
              >
                {Array.from({ length: 60 }, (_, i) => (
                  <option key={i} value={i.toString().padStart(2, '0')}>
                    {i.toString().padStart(2, '0')}
                  </option>
                ))}
              </select>
            </div>

            <div>
              <label className="form-label">Second</label>
              <select
                name="launchSecond"
                value={formData.launchSecond}
                onChange={handleInputChange}
                className="form-input"
              >
                {Array.from({ length: 60 }, (_, i) => (
                  <option key={i} value={i.toString().padStart(2, '0')}>
                    {i.toString().padStart(2, '0')}
                  </option>
                ))}
              </select>
            </div>
          </div>

            <div className="mt-4 p-3 bg-blue-900/20 border border-blue-500/20 rounded-lg">
              <p className="text-sm">
                <FiCalendar className="inline mr-2" />
              {getLaunchTimePreview()}
              </p>
            </div>
        </div>

        {/* Estimated Costs Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiDollarSign className="text-blue-400" />
            Estimated Costs
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
            <div className="cost-item">
              <div className="text-sm text-secondary mb-1">Creation Fee</div>
              <div className="text-xl font-bold">{creationFee} ETH</div>
              <div className="text-sm text-tertiary"> ${(creationFee * ethPrice).toFixed(2)}</div>
            </div>

            <div className="cost-item">
              <div className="text-sm text-secondary mb-1">Initial Buy</div>
              <div className="text-xl font-bold">{initialBuy.toFixed(3)} ETH</div>
              <div className="text-sm text-tertiary"> ${(initialBuy * ethPrice).toFixed(2)}</div>
            </div>

            <div className="cost-total">
              <div className="text-sm mb-1">Total</div>
              <div className="text-xl font-bold">{totalCost.toFixed(3)} ETH</div>
              <div className="text-sm"> ${(totalCost * ethPrice).toFixed(2)}</div>
            </div>
          </div>

          <button
            type="submit"
            className="btn-primary w-full py-4 text-lg"
            disabled={Object.keys(errors).length > 0 || isSubmitting}
          >
            <FiDollarSign className="inline mr-2" />
            {isSubmitting ? 'Creating Token...' : `Create Token (${totalCost.toFixed(3)} ETH)`}
          </button>
        </div>
      </form>
    </div>
    </div>
  );
};

export default CreateToken; 