import React, { useState, useEffect } from 'react';
import { 
  FiInfo, 
  FiZap, 
  FiCalendar, 
  FiClock,
  FiDollarSign,
  FiAlertTriangle,
  FiShare2,
  FiAlignLeft,
  FiTrendingUp,
  FiUser,
  FiShield,
  FiCreditCard,
  FiSave,
  FiTrash2,
  FiArrowRight,
  FiSearch,
  FiList
} from 'react-icons/fi';

const ManageToken = ({ isDarkMode, toggleTheme, isWalletConnected, toggleWallet }) => {
  const [tokenSelected, setTokenSelected] = useState(false);
  const [tokenAddress, setTokenAddress] = useState('');
  const [selectedToken, setSelectedToken] = useState(null);
  const [availableTokens, setAvailableTokens] = useState([
    {
      address: '0x1234567890123456789012345678901234567890',
      name: 'BaseToken',
      symbol: 'BASE',
      description: 'The official token of the Base ecosystem'
    },
    {
      address: '0x0987654321098765432109876543210987654321',
      name: 'MyTime',
      symbol: 'MT',
      description: 'Time-based rewards token'
    },
    {
      address: '0xabcdef1234567890abcdef1234567890abcdef12',
      name: 'BABYS',
      symbol: 'BABYS',
      description: 'Next generation crypto token'
    }
  ]);

  const [formData, setFormData] = useState({
    description: 'The official token of the Base ecosystem. Designed for long-term growth and community-driven development on the Base blockchain. This innovative token features a unique bonding curve mechanism that ensures fair distribution and sustainable growth.',
    telegram: 'https://t.me/basetoken',
    website: 'https://basetoken.io',
    twitter: 'https://twitter.com/basetoken',
    tokenImage: 'https://ipfs.io/ipfs/QmS9fN6tXU9bYh7h4cX3JxXvV7Lg2bR8bX8XkY7XfX6Xk',
    newOwner: '',
    withdrawRecipient: '0x742df44e...',
    withdrawAmount: '0.502'
  });

  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState({});
  const [tradingOpen, setTradingOpen] = useState(true);
  const [ownershipRenounced, setOwnershipRenounced] = useState(false);
  const [ethPrice, setEthPrice] = useState(2600); // Mock ETH price
  const [availableBalance, setAvailableBalance] = useState(0.502);

  const handleTokenAddressSubmit = () => {
    if (!tokenAddress.trim()) {
      alert('Please enter a token address');
      return;
    }
    if (!isValidEthereumAddress(tokenAddress)) {
      alert('Please enter a valid Ethereum address');
      return;
    }
    // Here you would typically validate if the user owns this token
    setSelectedToken({
      address: tokenAddress,
      name: 'Custom Token',
      symbol: 'CUSTOM',
      description: 'Custom token from address'
    });
    setTokenSelected(true);
  };

  const handleTokenSelect = (token) => {
    setSelectedToken(token);
    setTokenSelected(true);
  };

  const handleBackToSelection = () => {
    setTokenSelected(false);
    setSelectedToken(null);
    setTokenAddress('');
  };

  // If no token is selected, show token selection interface
  if (!tokenSelected) {
    return (
      <div className="container mx-auto px-4 pt-28 pb-64 flex justify-center">
        <div className="w-full max-w-4xl">
        <div className="manage-token-container page-container">


          {/* Token Address Input */}
          <div className="form-card mb-8">
            <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
              <FiSearch className="text-blue-400" />
              Enter Token Address
            </h2>
            <div className="mb-4">
              <label className="form-label">Token Contract Address</label>
              <input
                type="text"
                value={tokenAddress}
                onChange={(e) => setTokenAddress(e.target.value)}
                className="form-input"
                placeholder="0x..."
              />
              <div className="form-help">Enter the contract address of the token you want to manage</div>
            </div>
            <button
              onClick={handleTokenAddressSubmit}
              className="btn-primary"
            >
              <FiArrowRight className="inline mr-2" />
              Load Token
            </button>
          </div>

          {/* Available Tokens List */}
          <div className="form-card">
            <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
              <FiList className="text-blue-400" />
              Your Created Tokens
            </h2>
            <div className="grid gap-4">
              {availableTokens.map((token, index) => (
                <div
                  key={index}
                  className="border border-gray-200 dark:border-gray-700 rounded-lg p-4 hover:bg-gray-50 dark:hover:bg-gray-800 cursor-pointer transition-colors"
                  onClick={() => handleTokenSelect(token)}
                >
                  <div className="flex items-center justify-between">
                    <div>
                      <h3 className="font-semibold text-lg">{token.name} ({token.symbol})</h3>
                      <p className="text-sm text-gray-600 dark:text-gray-400">{token.description}</p>
                      <p className="text-xs text-gray-500 dark:text-gray-500 font-mono mt-1">
                        {token.address.slice(0, 6)}...{token.address.slice(-4)}
                      </p>
                    </div>
                    <FiArrowRight className="text-gray-400" />
                  </div>
                </div>
              ))}
            </div>
            {availableTokens.length === 0 && (
              <div className="text-center py-8 text-gray-500 dark:text-gray-400">
                <p>No tokens found. Create a token first to manage it here.</p>
              </div>
            )}
            </div>
          </div>
        </div>
      </div>
    );
  }

  // Form validation
  const validateForm = (section) => {
    const newErrors = {};

    if (section === 'description' || section === 'all') {
      if (!formData.description.trim()) {
        newErrors.description = 'Description cannot be empty';
      } else if (formData.description.length > 3000) {
        newErrors.description = 'Description must be 3000 characters or less';
      }
    }

    if (section === 'social' || section === 'all') {
      const urlFields = ['telegram', 'website', 'twitter'];
      urlFields.forEach(field => {
        if (formData[field] && !isValidUrl(formData[field])) {
          newErrors[field] = 'Please enter a valid URL';
        }
      });
    }

    if (section === 'ownership' || section === 'all') {
      if (formData.newOwner && !isValidEthereumAddress(formData.newOwner)) {
        newErrors.newOwner = 'Please enter a valid Ethereum address';
      }
    }

    if (section === 'withdraw' || section === 'all') {
      if (!isValidEthereumAddress(formData.withdrawRecipient)) {
        newErrors.withdrawRecipient = 'Please enter a valid Ethereum address';
      }
      if (!formData.withdrawAmount || parseFloat(formData.withdrawAmount) <= 0) {
        newErrors.withdrawAmount = 'Please enter a valid amount';
      } else if (parseFloat(formData.withdrawAmount) > availableBalance) {
        newErrors.withdrawAmount = 'Amount exceeds available balance';
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const isValidUrl = (string) => {
    try {
      new URL(string);
      return true;
    } catch (_) {
      return false;
    }
  };

  const isValidEthereumAddress = (address) => {
    return /^0x[a-fA-F0-9]{40}$/.test(address);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleSaveDescription = async () => {
    if (!validateForm('description')) return;
    
    setIsSubmitting(prev => ({ ...prev, description: true }));
    try {
      // Simulate API call for setDescription
      await new Promise(resolve => setTimeout(resolve, 1500));
      console.log('setDescription called:', formData.description);
      showNotification('Description updated successfully!', 'success');
    } catch (error) {
      console.error('Error updating description:', error);
      showNotification('Error updating description. Please try again.', 'error');
    } finally {
      setIsSubmitting(prev => ({ ...prev, description: false }));
    }
  };

  const handleSaveSocialLinks = async () => {
    if (!validateForm('social')) return;
    
    setIsSubmitting(prev => ({ ...prev, social: true }));
    try {
      // Simulate API call for setSocialLinks
      await new Promise(resolve => setTimeout(resolve, 1500));
      console.log('setSocialLinks called:', {
        telegram: formData.telegram,
        website: formData.website,
        twitter: formData.twitter,
        tokenImage: formData.tokenImage
      });
      showNotification('Social links updated successfully!', 'success');
    } catch (error) {
      console.error('Error updating social links:', error);
      showNotification('Error updating social links. Please try again.', 'error');
    } finally {
      setIsSubmitting(prev => ({ ...prev, social: false }));
    }
  };

  const handleOpenTrading = async () => {
    if (!window.confirm('Are you sure you want to open trading immediately? This action cannot be undone.')) {
      return;
    }
    
    setIsSubmitting(prev => ({ ...prev, trading: true }));
    try {
      // Simulate API call for openTradingNow
      await new Promise(resolve => setTimeout(resolve, 2000));
      console.log('openTradingNow called');
      setTradingOpen(true);
      showNotification('Trading opened successfully!', 'success');
    } catch (error) {
      console.error('Error opening trading:', error);
      showNotification('Error opening trading. Please try again.', 'error');
    } finally {
      setIsSubmitting(prev => ({ ...prev, trading: false }));
    }
  };

  const handleTransferOwnership = async () => {
    if (!validateForm('ownership')) return;
    
    if (!window.confirm(`Are you sure you want to transfer ownership to ${formData.newOwner}? This action cannot be undone.`)) {
      return;
    }
    
    setIsSubmitting(prev => ({ ...prev, transfer: true }));
    try {
      // Simulate API call for transferOwnership
      await new Promise(resolve => setTimeout(resolve, 2000));
      console.log('transferOwnership called:', formData.newOwner);
      showNotification('Ownership transferred successfully!', 'success');
      setFormData(prev => ({ ...prev, newOwner: '' }));
    } catch (error) {
      console.error('Error transferring ownership:', error);
      showNotification('Error transferring ownership. Please try again.', 'error');
    } finally {
      setIsSubmitting(prev => ({ ...prev, transfer: false }));
    }
  };

  const handleRenounceOwnership = async () => {
    if (!window.confirm('WARNING: Are you absolutely sure you want to renounce ownership? This action is irreversible and you will lose all control over the token.')) {
      return;
    }
    
    if (!window.confirm('This is your final warning. Renouncing ownership cannot be undone. Are you sure?')) {
      return;
    }
    
    setIsSubmitting(prev => ({ ...prev, renounce: true }));
    try {
      // Simulate API call for renounceOwnership
      await new Promise(resolve => setTimeout(resolve, 2000));
      console.log('renounceOwnership called');
      setOwnershipRenounced(true);
      showNotification('Ownership renounced successfully!', 'success');
    } catch (error) {
      console.error('Error renouncing ownership:', error);
      showNotification('Error renouncing ownership. Please try again.', 'error');
    } finally {
      setIsSubmitting(prev => ({ ...prev, renounce: false }));
    }
  };

  const handleWithdrawETH = async () => {
    if (!validateForm('withdraw')) return;
    
    setIsSubmitting(prev => ({ ...prev, withdraw: true }));
    try {
      // Simulate API call for withdrawETH
      await new Promise(resolve => setTimeout(resolve, 2000));
      console.log('withdrawETH called:', {
        recipient: formData.withdrawRecipient,
        amount: formData.withdrawAmount
      });
      setAvailableBalance(prev => prev - parseFloat(formData.withdrawAmount));
      showNotification('ETH withdrawal initiated successfully!', 'success');
      setFormData(prev => ({ ...prev, withdrawAmount: '' }));
    } catch (error) {
      console.error('Error withdrawing ETH:', error);
      showNotification('Error withdrawing ETH. Please try again.', 'error');
    } finally {
      setIsSubmitting(prev => ({ ...prev, withdraw: false }));
    }
  };

  const showNotification = (message, type = 'success') => {
    // In a real app, this would show a toast notification
    console.log(`${type.toUpperCase()}: ${message}`);
  };

  return (
    <div className="container mx-auto px-4 pt-28 pb-64 flex justify-center">
      <div className="w-full max-w-4xl">
      <div className="manage-token-container page-container">
        {/* Token Info Header */}
        <div className="mb-8">
          <button
            onClick={handleBackToSelection}
            className="btn-secondary mb-4"
          >
            <FiArrowRight className="inline mr-2 rotate-180" />
            Back to Token Selection
          </button>
          <div className="form-card">
            <div className="flex items-center justify-between">
              <div>
                <h1 className="text-2xl font-bold gradient-text">{selectedToken.name} ({selectedToken.symbol})</h1>
                <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
                  Contract: {selectedToken.address}
                </p>
              </div>
              <div className="text-right">
                <div className="text-sm text-gray-500 dark:text-gray-400">Token Address</div>
                <div className="font-mono text-sm">{selectedToken.address.slice(0, 6)}...{selectedToken.address.slice(-4)}</div>
              </div>
            </div>
          </div>
        </div>

        {/* Token Description Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiAlignLeft className="text-blue-400" />
            Token Description
          </h2>
          <div className="mb-4">
            <label className="form-label">Description</label>
            <textarea
              name="description"
              value={formData.description}
              onChange={handleInputChange}
              className={`form-input min-h-[120px] resize-none ${errors.description ? 'border-red-500' : ''}`}
              placeholder="Describe your token and project..."
              maxLength={3000}
            />
            {errors.description && <div className="form-error">{errors.description}</div>}
            <div className={`char-counter ${formData.description.length > 2800 ? 'warning' : ''} ${formData.description.length > 3000 ? 'error' : ''}`}>
              {formData.description.length}/3000 characters
            </div>
            <div className="form-help">Max 3000 characters (required for token visibility)</div>
          </div>
          <button
            onClick={handleSaveDescription}
            disabled={isSubmitting.description || errors.description}
            className="btn-primary"
          >
            <FiSave className="inline mr-2" />
            {isSubmitting.description ? 'Saving...' : 'Save Description'}
          </button>
        </div>

        {/* Social Links Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiShare2 className="text-blue-400" />
            Social Links
          </h2>
          <div className="social-links-grid mb-4">
            <div>
              <label className="form-label">Telegram</label>
              <input
                type="url"
                name="telegram"
                value={formData.telegram}
                onChange={handleInputChange}
                className={`form-input ${errors.telegram ? 'border-red-500' : ''}`}
                placeholder="https://t.me/yourtoken"
              />
              {errors.telegram && <div className="form-error">{errors.telegram}</div>}
              <div className="form-help">Your Telegram group link</div>
            </div>

            <div>
              <label className="form-label">Website</label>
              <input
                type="url"
                name="website"
                value={formData.website}
                onChange={handleInputChange}
                className={`form-input ${errors.website ? 'border-red-500' : ''}`}
                placeholder="https://yourtoken.com"
              />
              {errors.website && <div className="form-error">{errors.website}</div>}
              <div className="form-help">Your project's website</div>
            </div>

            <div>
              <label className="form-label">X/Twitter</label>
              <input
                type="url"
                name="twitter"
                value={formData.twitter}
                onChange={handleInputChange}
                className={`form-input ${errors.twitter ? 'border-red-500' : ''}`}
                placeholder="https://twitter.com/yourusername"
              />
              {errors.twitter && <div className="form-error">{errors.twitter}</div>}
              <div className="form-help">Your X/Twitter profile link</div>
            </div>
          </div>
          <button
            onClick={handleSaveSocialLinks}
            disabled={isSubmitting.social || Object.keys(errors).some(key => ['telegram', 'website', 'twitter'].includes(key))}
            className="btn-primary"
          >
            <FiSave className="inline mr-2" />
            {isSubmitting.social ? 'Saving...' : 'Save Social Links'}
          </button>
        </div>

        {/* Trading Management Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiTrendingUp className="text-blue-400" />
            Trading Management
          </h2>
          <div className="info-box mb-4">
            <div className="flex items-start space-x-3">
              <FiInfo className="text-blue-400 mt-1 flex-shrink-0" size={20} />
              <div>
                <h3 className="font-semibold mb-2">Trading Status</h3>
                <p className="text-sm">
                  This token is currently <strong>{tradingOpen ? 'trading open' : 'scheduled for launch'}</strong>. 
                  {tradingOpen ? ' Trading is available on the platform.' : ' Trading will begin at the scheduled launch time.'}
                </p>
              </div>
            </div>
          </div>
          {!tradingOpen && (
            <>
              <div className="mb-4">
                <label className="form-label">Open Trading Now</label>
                <div className="form-help">Immediately open trading for your token, bypassing scheduled launch time.</div>
                <div className="warning-box">
                  <div className="flex items-start space-x-3">
                    <FiAlertTriangle className="text-red-400 mt-1 flex-shrink-0" size={20} />
                    <div>
                      <h3 className="font-semibold mb-2">Important Note</h3>
                      <p className="text-sm">This action cannot be undone. Once trading is opened, it cannot be closed again.</p>
                    </div>
                  </div>
                </div>
              </div>
              <button
                onClick={handleOpenTrading}
                disabled={isSubmitting.trading}
                className="btn-instant"
              >
                <FiZap className="inline mr-2" />
                {isSubmitting.trading ? 'Opening Trading...' : 'Open Trading Now'}
              </button>
            </>
          )}
        </div>

        <div className="section-divider"></div>

        {/* Ownership Management Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiShield className="text-blue-400" />
            Ownership Management
          </h2>
          
          <div className="mb-6">
            <label className="form-label">Transfer Ownership</label>
            <input
              type="text"
              name="newOwner"
              value={formData.newOwner}
              onChange={handleInputChange}
              className={`form-input ${errors.newOwner ? 'border-red-500' : ''}`}
              placeholder="0x..."
              disabled={ownershipRenounced}
            />
            {errors.newOwner && <div className="form-error">{errors.newOwner}</div>}
            <div className="form-help">Transfer ownership to a new address. The new owner will gain full control of the token.</div>
            <div className="warning-box">
              <div className="flex items-start space-x-3">
                <FiAlertTriangle className="text-red-400 mt-1 flex-shrink-0" size={20} />
                <div>
                  <h3 className="font-semibold mb-2">Warning</h3>
                  <p className="text-sm">Transferring ownership is permanent. Ensure the new owner address is correct as there is no way to revert this action.</p>
                </div>
              </div>
            </div>
            <button
              onClick={handleTransferOwnership}
              disabled={isSubmitting.transfer || ownershipRenounced || !formData.newOwner}
              className="btn-primary"
            >
              <FiArrowRight className="inline mr-2" />
              {isSubmitting.transfer ? 'Transferring...' : 'Transfer Ownership'}
            </button>
          </div>
          
          <div className="mb-4">
            <label className="form-label">Renounce Ownership</label>
            <div className="form-help">Permanently renounce ownership of the token contract. This action cannot be undone.</div>
            <div className="warning-box">
              <div className="flex items-start space-x-3">
                <FiAlertTriangle className="text-red-400 mt-1 flex-shrink-0" size={20} />
                <div>
                  <h3 className="font-semibold mb-2">Critical Warning</h3>
                  <p className="text-sm">Renouncing ownership means you will lose all administrative control over the token. This action is irreversible and cannot be undone.</p>
                </div>
              </div>
            </div>
            <button
              onClick={handleRenounceOwnership}
              disabled={isSubmitting.renounce || ownershipRenounced}
              className="btn-danger"
            >
              <FiTrash2 className="inline mr-2" />
              {ownershipRenounced ? 'Ownership Renounced' : isSubmitting.renounce ? 'Renouncing...' : 'Renounce Ownership'}
            </button>
          </div>
        </div>

        {/* Fund Management Section */}
        <div className="form-card">
          <h2 className="text-2xl font-bold mb-6 gradient-text flex items-center gap-3">
            <FiCreditCard className="text-blue-400" />
            Fund Management
          </h2>
          <div className="info-box mb-4">
            <div className="flex items-start space-x-3">
              <FiInfo className="text-blue-400 mt-1 flex-shrink-0" size={20} />
              <div>
                <h3 className="font-semibold mb-2">Available Balance</h3>
                <p className="text-sm">
                  <strong>{availableBalance.toFixed(3)} ETH</strong> available for withdrawal ( ${(availableBalance * ethPrice).toFixed(2)})
                </p>
              </div>
            </div>
          </div>
          <div className="fund-management-grid mb-4">
            <div>
              <label className="form-label">Recipient Address</label>
              <input
                type="text"
                name="withdrawRecipient"
                value={formData.withdrawRecipient}
                onChange={handleInputChange}
                className={`form-input ${errors.withdrawRecipient ? 'border-red-500' : ''}`}
                placeholder="0x..."
              />
              {errors.withdrawRecipient && <div className="form-error">{errors.withdrawRecipient}</div>}
            </div>

            <div>
              <label className="form-label">Amount (ETH)</label>
              <div className="relative">
                <input
                  type="number"
                  name="withdrawAmount"
                  value={formData.withdrawAmount}
                  onChange={handleInputChange}
                  className={`form-input pr-12 ${errors.withdrawAmount ? 'border-red-500' : ''}`}
                  placeholder="0.0"
                  step="0.001"
                  min="0.001"
                  max={availableBalance}
                />
                <div className="absolute right-3 top-1/2 transform -translate-y-1/2 text-secondary font-medium">
                  ETH
                </div>
              </div>
              {errors.withdrawAmount && <div className="form-error">{errors.withdrawAmount}</div>}
            </div>
          </div>
          <div className="form-help mb-4">Withdraw ETH from the token contract to the specified address.</div>
          <button
            onClick={handleWithdrawETH}
            disabled={isSubmitting.withdraw || Object.keys(errors).some(key => ['withdrawRecipient', 'withdrawAmount'].includes(key)) || !formData.withdrawAmount}
            className="btn-primary"
          >
            <FiCreditCard className="inline mr-2" />
            {isSubmitting.withdraw ? 'Withdrawing...' : 'Withdraw ETH'}
          </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManageToken; 