import React, { useState, useEffect } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import TokenCard from '../components/TokenCard';
import { 
  FiTrendingUp, 
  FiRefreshCw, 
  FiArrowRight,
  FiBarChart,
  FiZap,
  FiClock
} from 'react-icons/fi';
import './TrendingTokensUpdated.css';

const TrendingTokensUpdated = ({ isDarkMode }) => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState('market-cap');
  const [currentPage, setCurrentPage] = useState(1);
  const tokensPerPage = 12;

  // Handle URL tab parameter
  useEffect(() => {
    const tabParam = searchParams.get('tab');
    if (tabParam === 'recently-migrated') {
      setActiveTab('recently-migrated');
    }
  }, [searchParams]);

  // Reset to page 1 when tab changes
  useEffect(() => {
    setCurrentPage(1);
  }, [activeTab]);

  // Generate market cap tokens with new TokenCard format
  const generateMarketCapTokens = () => {
    const baseTokens = [
      {
        id: 1,
        logo: 'B',
        name: 'BaseToken',
        symbol: 'BASE  Ethereum',
        contractAddress: '0x742d...35Cc',
        status: 'migrated',
        marketCap: '$2.5B',
        volume24h: '$456.2M',
        migrationThreshold: '50 ETH',
        timeRemaining: 'Completed',
        progress: 100,
        socials: {
          website: 'https://basetoken.com',
          telegram: 'https://t.me/basetoken',
          twitter: 'https://twitter.com/basetoken'
        }
      },
      {
        id: 2,
        logo: 'D',
        name: 'DexFi',
        symbol: 'DEXF  Base',
        contractAddress: '0x7Be8...D12b',
        status: 'migrating',
        marketCap: '$1.2B',
        volume24h: '$234.1M',
        migrationThreshold: '75 ETH',
        timeRemaining: '2d 12h',
        progress: 65,
        socials: {
          website: 'https://dexfi.com',
          telegram: 'https://t.me/dexfi',
          twitter: 'https://twitter.com/dexfi'
        }
      },
      {
        id: 3,
        logo: 'C',
        name: 'CurveCoin',
        symbol: 'CRV  Ethereum',
        contractAddress: '0x34aA...fDF3',
        status: 'migrating',
        marketCap: '$3.2B',
        volume24h: '$610.0M',
        migrationThreshold: '100 ETH',
        timeRemaining: '1d 8h',
        progress: 78,
        socials: {
          website: 'https://curvecoin.com',
          telegram: 'https://t.me/curvecoin',
          twitter: 'https://twitter.com/curvecoin'
        }
      }
    ];

    // Generate additional tokens
    const additionalTokens = [];
    const tokenNames = [
      "LiquidFi", "Synthetix", "Aave", "Compound", "Maker", "Uniswap", "SushiSwap", "PancakeSwap",
      "Yearn", "Balancer", "Ren", "Loopring", "Optimism", "Arbitrum", "Polygon", "Avalanche"
    ];

    for (let i = 4; i <= 20; i++) {
      const name = tokenNames[i - 4] || `Token${i}`;
      const symbol = name.substring(0, 4).toUpperCase();
      const marketCap = Math.floor(Math.random() * 5000000000) + 100000000;
      const volume24h = Math.floor(Math.random() * 500000000) + 50000000;
      const migrationThreshold = (Math.random() * 100 + 10).toFixed(1);
      const progress = Math.floor(Math.random() * 100) + 1;
      
      // Determine status based on progress
      let status = 'new';
      if (progress === 100) status = 'migrated';
      else if (progress > 50) status = 'migrating';
      
      // Generate time remaining based on status
      let timeRemaining = '1d 5h';
      if (status === 'migrated') timeRemaining = 'Completed';
      else if (status === 'migrating') timeRemaining = `${Math.floor(Math.random() * 3) + 1}d ${Math.floor(Math.random() * 24)}h`;
      else timeRemaining = `${Math.floor(Math.random() * 7) + 1}d ${Math.floor(Math.random() * 24)}h`;
      
      // Generate a mock Ethereum address
      const mockAddress = `0x${Math.random().toString(16).substring(2, 42).padEnd(40, '0')}`;
      const shortAddress = `${mockAddress.substring(0, 6)}...${mockAddress.substring(38)}`;

      additionalTokens.push({
        id: i,
        logo: name.charAt(0),
        name: name,
        symbol: `${symbol}  ${Math.random() > 0.5 ? 'Ethereum' : 'Base'}`,
        contractAddress: shortAddress,
        status: status,
        marketCap: `$${(marketCap / 1000000000).toFixed(1)}B`,
        volume24h: `$${(volume24h / 1000000).toFixed(1)}M`,
        migrationThreshold: `${migrationThreshold} ETH`,
        timeRemaining: timeRemaining,
        progress: progress,
        socials: {
          website: `https://${name.toLowerCase()}.com`,
          telegram: `https://t.me/${name.toLowerCase()}`,
          twitter: `https://twitter.com/${name.toLowerCase()}`
        }
      });
    }

    return [...baseTokens, ...additionalTokens];
  };

  // Generate recently migrated tokens
  const generateRecentlyMigratedTokens = () => {
    const migratedTokens = [];
    const tokenNames = [
      "BaseToken", "DexFi", "CurveCoin", "LiquidFi", "Synthetix", "Aave", "Compound", "Maker",
      "Uniswap", "SushiSwap", "PancakeSwap", "Yearn", "Balancer", "Ren", "Loopring", "Optimism"
    ];

    for (let i = 1; i <= 16; i++) {
      const name = tokenNames[i - 1] || `Token${i}`;
      const symbol = name.substring(0, 4).toUpperCase();
      const marketCap = Math.floor(Math.random() * 3000000000) + 500000000;
      const volume24h = Math.floor(Math.random() * 300000000) + 100000000;
      const migrationThreshold = (Math.random() * 80 + 20).toFixed(1);
      
      // Generate a mock Ethereum address
      const mockAddress = `0x${Math.random().toString(16).substring(2, 42).padEnd(40, '0')}`;
      const shortAddress = `${mockAddress.substring(0, 6)}...${mockAddress.substring(38)}`;

      migratedTokens.push({
        id: i,
        logo: name.charAt(0),
        name: name,
        symbol: `${symbol}  ${Math.random() > 0.5 ? 'Ethereum' : 'Base'}`,
        contractAddress: shortAddress,
        status: 'migrated',
        marketCap: `$${(marketCap / 1000000000).toFixed(1)}B`,
        volume24h: `$${(volume24h / 1000000).toFixed(1)}M`,
        migrationThreshold: `${migrationThreshold} ETH`,
        timeRemaining: 'Completed',
        progress: 100,
        socials: {
          website: `https://${name.toLowerCase()}.com`,
          telegram: `https://t.me/${name.toLowerCase()}`,
          twitter: `https://twitter.com/${name.toLowerCase()}`
        }
      });
    }

    return migratedTokens;
  };

  const marketCapTokens = generateMarketCapTokens();
  const recentlyMigratedTokens = generateRecentlyMigratedTokens();

  const getCurrentTokens = () => {
    if (activeTab === 'market-cap') {
      return marketCapTokens;
    } else if (activeTab === 'recently-migrated') {
      return recentlyMigratedTokens;
    }
    return marketCapTokens;
  };

  const currentTokens = getCurrentTokens();
  const totalPages = Math.ceil(currentTokens.length / tokensPerPage);
  const startIndex = (currentPage - 1) * tokensPerPage;
  const endIndex = startIndex + tokensPerPage;
  const displayedTokens = currentTokens.slice(startIndex, endIndex);

  const handleTabChange = (tab) => {
    setActiveTab(tab);
    navigate(`/track?tab=${tab}`);
  };

  const handlePreviousPage = () => {
    setCurrentPage(prev => Math.max(prev - 1, 1));
  };

  const handleNextPage = () => {
    setCurrentPage(prev => Math.min(prev + 1, totalPages));
  };

  const handleCardClick = (token) => {
    navigate(`/swap?token=${token.id}`);
  };

  return (
    <div className="trending-tokens-container">
      {/* Header */}
      <div className="trending-header">
        <div className="trending-title">
          <FiTrendingUp className="trending-icon" />
          <h1>Trending Tokens</h1>
        </div>
      </div>

      {/* Tab Navigation */}
      <div className="tab-navigation">
        <button
          className={`tab-btn ${activeTab === 'market-cap' ? 'active' : ''}`}
          onClick={() => handleTabChange('market-cap')}
        >
          <FiBarChart className="tab-icon" />
          Market Cap
        </button>
        <button
          className={`tab-btn ${activeTab === 'recently-migrated' ? 'active' : ''}`}
          onClick={() => handleTabChange('recently-migrated')}
        >
          <FiZap className="tab-icon" />
          Recently Migrated
        </button>
      </div>

      {/* Token Cards Grid */}
      <div className="token-cards-grid">
        {displayedTokens.map((token) => (
          <div key={token.id} className="token-card-wrapper" onClick={() => handleCardClick(token)}>
            <TokenCard token={token} />
          </div>
        ))}
      </div>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="pagination-container">
          <button 
            onClick={handlePreviousPage} 
            disabled={currentPage === 1}
            className="pagination-btn"
          >
            Previous
          </button>
          <span className="pagination-info">
            Page {currentPage} of {totalPages}
          </span>
          <button 
            onClick={handleNextPage} 
            disabled={currentPage === totalPages}
            className="pagination-btn"
          >
            Next
          </button>
        </div>
      )}
    </div>
  );
};

export default TrendingTokensUpdated; 