import React, { useState, useEffect } from 'react';
import { 
  FiCreditCard, 
  FiCopy, 
  FiRefreshCw, 
  FiCheck,
  FiPower,
  FiDollarSign,
  FiTrendingUp,
  FiInfo
} from 'react-icons/fi';
import { useNavigate } from 'react-router-dom';

const Wallet = ({ currentPage, onNavigate }) => {
  const navigate = useNavigate();
  const [isConnected, setIsConnected] = useState(true);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [showNotification, setShowNotification] = useState(false);
  const [notificationMessage, setNotificationMessage] = useState('');

  // Mock wallet data
  const walletData = {
    address: '0x742df44e8b8b8b8b8b8b8b8b8b8b8b8b8b8b8b8b8',
    ethBalance: 1.254,
    fiatValue: 3260.50,
    tokens: [
      {
        symbol: 'B',
        name: 'BaseToken',
        ticker: 'BASE',
        balance: 1250,
        value: 3050.00
      },
      {
        symbol: 'D',
        name: 'DexFi',
        ticker: 'DEXF',
        balance: 500,
        value: 610.00,
        gradient: 'linear-gradient(135deg, #0ea5e9, #10b981)'
      }
    ],
    recentTransactions: [
      {
        hash: '0xabc123...',
        type: 'Send',
        amount: '-0.5 ETH',
        date: '2024-06-01',
        status: 'Success',
        to: '0x1234...abcd'
      },
      {
        hash: '0xdef456...',
        type: 'Receive',
        amount: '+1.2 ETH',
        date: '2024-05-30',
        status: 'Success',
        from: '0xabcd...1234'
      },
      {
        hash: '0xghi789...',
        type: 'Swap',
        amount: '-100 BASE',
        date: '2024-05-28',
        status: 'Pending',
        to: 'Uniswap'
      }
    ]
  };

  const showNotificationMessage = (message) => {
    setNotificationMessage(message);
    setShowNotification(true);
    setTimeout(() => setShowNotification(false), 2000);
  };

  const handleConnectWallet = () => {
    setIsConnected(true);
    showNotificationMessage('Wallet connected successfully!');
  };

  const handleDisconnectWallet = () => {
    setIsConnected(false);
    showNotificationMessage('Wallet disconnected');
  };

  const handleCopyAddress = () => {
    navigator.clipboard.writeText(walletData.address);
    showNotificationMessage('Address copied to clipboard!');
  };

  const handleRefreshBalance = () => {
    setIsRefreshing(true);
    setTimeout(() => {
      setIsRefreshing(false);
      showNotificationMessage('Balance updated successfully!');
    }, 800);
  };

  const formatAddress = (address) => {
    return `${address.slice(0, 6)}...${address.slice(-4)}`;
  };

  return (
    <div className="container mx-auto px-4 pt-28 pb-64 flex justify-center">
      <div className="w-full max-w-4xl">
        <div className="wallet-container page-container">


          {isConnected ? (
            <>
              {/* Wallet Balance Section */}
              <div className="wallet-section wallet-balance-card">
                <div className="section-header">
                  <FiDollarSign className="inline mr-2" />
                  <span>Wallet Balance</span>
                </div>
                <div className="wallet-address">
                  <FiCreditCard className="inline mr-2" />
                  <span>{formatAddress(walletData.address)}</span>
                  <button className="action-btn copy-btn" onClick={handleCopyAddress}>
                    <FiCopy className="inline mr-2" /> Copy
                  </button>
                </div>
                <div className="balance-amount">
                  {walletData.ethBalance} ETH
                  <div className="balance-fiat"> ${walletData.fiatValue.toLocaleString()}</div>
                </div>
                <div className="wallet-actions">
                  <button className="action-btn refresh-btn" onClick={handleRefreshBalance}>
                    <FiRefreshCw className={`inline mr-2 ${isRefreshing ? 'animate-spin' : ''}`} />
                    {isRefreshing ? 'Refreshing...' : 'Refresh'}
                  </button>
                  <button className="action-btn disconnect-btn" onClick={handleDisconnectWallet}>
                    <FiPower className="inline mr-2" /> Disconnect
                  </button>
                </div>
              </div>

              {/* Token Holdings Section */}
              <div className="wallet-section tokens-section">
                <div className="section-header">
                  <FiTrendingUp className="inline mr-2 text-blue-400" />
                  <span>Token Holdings</span>
                  <span className="text-secondary ml-2">{walletData.tokens.length} tokens</span>
                </div>
                <div className="tokens-list scrollable-list">
                  {walletData.tokens.map((token, index) => (
                    <div
                      key={index}
                      className="token-item"
                      onClick={() => navigate(`/swap?token=${token.ticker}`)}
                      style={{ cursor: 'pointer' }}
                    >
                      <div className="token-logo" style={token.gradient ? { background: token.gradient } : {}}>
                        {token.symbol}
                      </div>
                      <div className="token-info modern-align">
                        <div className="token-name">{token.name}</div>
                        <div className="token-symbol">{token.ticker}</div>
                      </div>
                      <div className="token-balance">
                        <div className="token-amount">
                          {token.balance.toLocaleString()} {token.ticker}
                        </div>
                        <div className="token-value"> ${token.value.toLocaleString()}</div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* Recent Transactions Section */}
              <div className="wallet-section transactions-section">
                <div className="section-header">
                  <FiInfo className="inline mr-2" />
                  <span>Recent Transactions</span>
                </div>
                <div className="transactions-list scrollable-list">
                  {walletData.recentTransactions.length === 0 ? (
                    <div className="no-transactions">No recent transactions</div>
                  ) : (
                    walletData.recentTransactions.map((tx, idx) => (
                      <div key={idx} className={`transaction-item ${tx.status.toLowerCase()}`}> 
                        <div className="transaction-type">{tx.type}</div>
                        <div className="transaction-amount">{tx.amount}</div>
                        <div className="transaction-date">{tx.date}</div>
                        <div className="transaction-status">{tx.status}</div>
                        {tx.to && <div className="transaction-to">To: {tx.to}</div>}
                        {tx.from && <div className="transaction-from">From: {tx.from}</div>}
                        <div className="transaction-hash">Hash: {tx.hash}</div>
                      </div>
                    ))
                  )}
                </div>
              </div>
            </>
          ) : (
            <div className="disconnected-state">
              <div className="connect-wallet-container">
                <div className="connect-wallet-icon">
                  <FiCreditCard />
                </div>
                <h2 className="connect-wallet-text">Connect Your Wallet</h2>
                <p className="connect-wallet-subtext">
                  Connect your Ethereum wallet to view your token holdings and interact with the platform
                </p>
                <button className="btn connect-btn" onClick={handleConnectWallet}>
                  <FiCreditCard className="inline mr-2" /> Connect Wallet
                </button>
              </div>
            </div>
          )}

          {/* Copy Notification */}
          {showNotification && (
            <div className="copy-notification show">
              {notificationMessage}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Wallet; 