import React from 'react';
import { FiGlobe, FiMessageCircle, FiTwitter, FiRepeat, FiStar, FiTrendingUp, FiZap, FiBarChart, FiShare2, FiClock } from 'react-icons/fi';
import './TokenCard.css';

const TokenCard = ({ 
  token = {
    logo: 'B',
    name: 'BaseToken',
    symbol: 'BASE  Ethereum',
    contractAddress: '0x742d...35Cc',
    status: 'New',  // 'upcoming', 'New', 'Bonding', 'Migrated'
    marketCap: '$12.5B',
    volume24h: '$456.2M',
    migrationThreshold: '50 ETH',
    timeRemaining: '1d 5h',
    progress: 35,
    socials: {
      website: '#',
      telegram: '#',
      twitter: '#'
    }
  }
}) => {

  // Copy token address to clipboard
  const handleCopyAddress = (e, address) => {
    e.stopPropagation(); // Prevent card click
    navigator.clipboard.writeText(address).then(() => {
      // You could add a toast notification here
      console.log('Address copied to clipboard:', address);
    }).catch(err => {
      console.error('Failed to copy address:', err);
    });
  };

  return (
    <div className="main-token-card">
      <div className="token-header">
        <div className="token-logo-lg" style={{ width: '80px', height: '80px', minWidth: '80px' }}>
          {token.logo && token.logo.startsWith('http') ? (
            <img src={token.logo} alt={`${token.name} Logo`} style={{ width: '100%', height: '100%' }} />
          ) : (
            <span className="token-logo-text" style={{ fontSize: '28px' }}>{token.logo || token.name.charAt(0)}</span>
          )}
        </div>
        <div className="token-info">
          <div className="token-name" style={{ fontSize: '16px' }}>{token.name}</div>
          <div className="token-symbol" style={{ fontSize: '12px' }}>{token.symbol}</div>
          <div 
            className="contract-address" 
            style={{ fontSize: '10px', cursor: 'pointer' }}
            onClick={(e) => handleCopyAddress(e, token.contractAddress)}
            title="Click to copy address"
          >
            {token.contractAddress}
          </div>
        </div>
        <div className={`status-badge ${token.status.toLowerCase()}`}>
          {token.status === 'Migrated' ? (
            <FiStar />
          ) : token.status === 'Bonding' ? (
            <FiTrendingUp />
          ) : token.status === 'New' ? (
            <FiZap />
          ) : token.status === 'upcoming' ? (
            <FiClock />
          ) : (
            <FiZap />
          )}
          {token.status}
        </div>
      </div>
      <div className="token-body">
        <div className="stats-container">
          <div className="stat-row" style={{ fontSize: '12px' }}>
            <span><FiBarChart size={12} /> Market Cap</span>
            <span className="stat-value">{token.marketCap}</span>
          </div>
          <div className="stat-row" style={{ fontSize: '12px' }}>
            <span><FiShare2 size={12} /> 24h Volume</span>
            <span className="stat-value">{token.volume24h}</span>
          </div>
          <div className="stat-row" style={{ fontSize: '12px' }}>
            <span><FiZap size={12} /> Migration Threshold</span>
            <span className="stat-value">{token.migrationThreshold}</span>
          </div>
          <div className="stat-row" style={{ fontSize: '12px' }}>
            <span><FiClock size={12} /> Time Remaining</span>
            <span className="stat-value">{token.timeRemaining}</span>
          </div>
        </div>
        <div className="progress-container">
          <div className="progress-fill" style={{ width: `${token.progress}%` }}></div>
        </div>
        <div className="progress-label" style={{ fontSize: '12px' }}>
          <span>Migration Progress</span>
          <span>{token.progress}%</span>
        </div>
      </div>
      <div className="token-footer">
        <div className="social-links">
          <a href={token.socials.website} className="social-btn" title="Website">
            <img src="/assets/website.png" alt="Website" className="social-icon" />
          </a>
          <a href={token.socials.telegram} className="social-btn" title="Telegram">
            <img src="/assets/telegram.png" alt="Telegram" className="social-icon" />
          </a>
          <a href={token.socials.twitter} className="social-btn" title="Twitter">
            <img src="/assets/x.png" alt="Twitter" className="social-icon" />
          </a>
        </div>
        <button className="swap-btn">
          <FiRepeat /> Swap
        </button>
      </div>
    </div>
  );
};

export default TokenCard;