import React, { useState, useEffect } from 'react';
import {
  FiSearch,
  FiMoon,
  FiSun,
  FiX,
  FiCreditCard
} from 'react-icons/fi';
import logoImage from '../assets/Logo.PNG';
import './ModernHeader.css';

const ModernHeader = ({ isDarkMode, onToggleTheme, currentPage, onNavigate, searchQuery = '', onSearch }) => {
  const [isVisible, setIsVisible] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const [isWalletConnected, setIsWalletConnected] = useState(false);
  const [isMobileSearchExpanded, setIsMobileSearchExpanded] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      
      // Hide header when scrolling down and past 50px
      if (currentScrollY > lastScrollY && currentScrollY > 50) {
        setIsVisible(false);
      } 
      // Show header when scrolling up or at the top
      else if (currentScrollY < lastScrollY || currentScrollY <= 50) {
        setIsVisible(true);
      }
      
      setLastScrollY(currentScrollY);
    };

    // Throttle scroll events for better performance
    let ticking = false;
    const throttledHandleScroll = () => {
      if (!ticking) {
        requestAnimationFrame(() => {
          handleScroll();
          ticking = false;
        });
        ticking = true;
      }
    };

    window.addEventListener('scroll', throttledHandleScroll, { passive: true });
    return () => window.removeEventListener('scroll', throttledHandleScroll);
  }, [lastScrollY]);

  const handleSearchChange = (e) => {
    const query = e.target.value;
    if (onSearch) {
      onSearch(query);
    }
  };

  const handleClearSearch = () => {
    if (onSearch) {
      onSearch('');
    }
  };

  const handleLogoClick = () => {
    onNavigate('home');
  };

  const handleWalletClick = () => {
    if (isWalletConnected) {
      // If connected, navigate to wallet page
      onNavigate('wallet');
    } else {
      // If disconnected, connect wallet
      setIsWalletConnected(true);
    }
  };

  const handleMobileSearchToggle = () => {
    setIsMobileSearchExpanded(!isMobileSearchExpanded);
  };

  const handleMobileSearchClose = () => {
    setIsMobileSearchExpanded(false);
  };

  return (
    <header className={`modern-header ${!isVisible ? 'header-hidden' : ''}`}>
      {/* Left Side - Logo */}
      <div className="header-left">
        <div className="logo" onClick={handleLogoClick}>
          <img src={logoImage} alt="RAVO Logo" className="logo-image" />
          <span className="logo-text">RAVO</span>
        </div>
      </div>

      {/* Center - Search Bar */}
      <div className="header-center">
        {/* Desktop Search Bar */}
        <div className="search-container desktop-search">
          <FiSearch className="search-icon" size={18} />
          <input
            type="text"
            placeholder="Search tokens, contracts, or creators..."
            value={searchQuery}
            onChange={handleSearchChange}
            className="search-input"
          />
          {searchQuery && (
            <button
              type="button"
              onClick={handleClearSearch}
              className="clear-search-btn"
            >
              <FiX size={16} />
            </button>
          )}
        </div>

        {/* Mobile Search Button */}
        <button
          onClick={handleMobileSearchToggle}
          className="mobile-search-btn"
          aria-label="Search"
        >
          <FiSearch size={18} />
        </button>

        {/* Mobile Expanded Search */}
        {isMobileSearchExpanded && (
          <div className="mobile-search-expanded">
            <div className="mobile-search-container">
              <FiSearch className="search-icon" size={18} />
              <input
                type="text"
                placeholder="Search tokens, contracts, or creators..."
                value={searchQuery}
                onChange={handleSearchChange}
                className="mobile-search-input"
                autoFocus
              />
              <button
                type="button"
                onClick={handleMobileSearchClose}
                className="mobile-search-close"
              >
                <FiX size={18} />
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Right Side - Wallet & Theme Toggle */}
      <div className="header-right">
        {/* Connect Wallet Button */}
        <button
          onClick={handleWalletClick}
          className={`wallet-btn ${isWalletConnected ? 'connected' : ''}`}
        >
          <FiCreditCard size={16} />
          <span>{isWalletConnected ? 'Wallet' : 'Connect'}</span>
        </button>

        {/* Theme Toggle */}
        <button
          onClick={onToggleTheme}
          className="theme-toggle-btn"
          aria-label="Toggle theme"
        >
          {isDarkMode ? <FiSun size={18} /> : <FiMoon size={18} />}
        </button>
      </div>
    </header>
  );
};

export default ModernHeader; 